import { Body, Controller, Get, HttpStatus, Next, Param, Post, Req, Res } from '@nestjs/common';
import dictionaryService  from './dictionary.service';
import { ITrie } from '@nxworspace/api-interfaces';
import { Response,NextFunction,Request } from 'express';

@Controller('trie')
export class DictionaryController {
    public dictionaryService=new dictionaryService();
    constructor() {
       
    }

  @Get()
  async getDictionaryTrie(@Res() res: Response): Promise<void> {
    
    const getDictionary: ITrie = await this.dictionaryService.getDictionary();
    res.status(HttpStatus.OK).json({ data: getDictionary, message: 'get Dictionary' });

  }

  @Get()
  async populateFromFile(@Res() res: Response,@Next()next: NextFunction): Promise<void> {
   
    try {
      const getDictionary: string = await this.dictionaryService.populateFromFile();

      res.status(200).json({ data: getDictionary, message: 'populated From File' });
    } catch (error) {
      next(error);
    }
  }

  @Get(':word')
  async isWordExists(@Param('word') word: string,@Res() res: Response,@Next()next: NextFunction): Promise<void> {
    try {
        
        const findword: boolean = await this.dictionaryService.searchForWord(word);
  
        res.status(200).json({ data: findword, message: 'word found = '+ findword });
      } catch (error) {
        next(error);
      }
  }
 
  @Post('bulkInsertWordsToTrie')
  async bulkInsertWordsToTrie(@Body() createCatDto: any,@Req() req: Request,@Res() res: Response,@Next()next: NextFunction): Promise<void> {
    try {
        const wordsArray: string[] = createCatDto;
        const bulkInsertWordsToTrie: string = await this.dictionaryService.bulkInsertWordsToTrie(wordsArray);
  
        res.status(201).json({  message: 'wordsInserted' });
      } catch (error) {
        next(error);
      }
      
  }
  

}



  

 

  